.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.over-image {
  stroke: #e36a25;
  stroke-width: 12px;
  width: 120px;
  height: 120px;
  transform: rotate(90deg);
}

.deg {
  display: inline-block;
  position: relative;
  width: 120px;
  height: 160px;
  overflow: hidden;
}

.deg span {
  width: 120px;
  height: 120px;
  line-height: 120px;
  position: absolute;
  left: 0;
  top: 0;
  text-align: center;
  font-size: 30px;
  font-weight: bold;
}

.red {
  stroke: red;
}

footer {
  background: #363842;
}

footer .social a {
  color: #363842;
}

header {
  background: #5ec2e7;
}

header h1 {
  font-size: 50px;
  line-height: 50px;
  margin: 0px;
  font-weight: 700;
  color: #f6f6f6;
  float: left;
}

header .prof {
  width: 100%;
  display: block;
}

header h2 {
  color: #ffffff;
  font-size: 60px;
  /*  -   */
  font-weight: 700;
  line-height: 77px;
  /*  -   */
  text-align: center;
  margin-bottom: 0;
}

header h3 {
  color: #ffffff;
  font-size: 18px;
  /*  -   */
  font-weight: 400;
  text-align: center;
  margin-top: 0;
  margin-bottom: 30px;
}

.logo {
  height: 80px;
  width: 100%;
  margin: 20px;
}

section {
  height: auto;
  overflow: hidden;
  text-align: center;
}

.content {
  padding: 20px 0px 20px 0px;
  overflow: hidden;
}

h2 {
  margin-bottom: 20px;
}

.item1 {
  background: #ff605f;
}

.item3 {
  background: #34cf75;
}

.item_video {
  height: 40vh;
}

.time_content {
  margin: 0 auto;
  position: relative;
}

.time_content .left {
  position: relative;
  right: calc(-50% + 300px);
  float: left;
  padding: 10px;
  border-radius: 5px;
}

.time_content .left h3 {
  text-align: right;
}

.time_content .left span {
  float: right;
}

.time_content p {
  background: #34cf75;
  border-radius: 5px;
  margin-bottom: 5px;
  text-align: left;
  padding: 5px;
  box-shadow: 0 3px 8px -2px rgba(55, 60, 60, 0.7);
  width: auto;
}

.time_content .right {
  padding: 10px;
  position: relative;
  right: calc(50% - 300px);
  float: right;
}

.time_content .right span {
  float: left;
}

.time_content .right h3 {
  text-align: left;
}

.time_content .item {
  width: 250px;
}

.time_content .timel {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.time_content .timeline {
  position: absolute;
  width: 3px;
  height: 100%;
  background: #f6f6f6;
  left: calc(50% - 1px);
  z-index: 9;
}

.time_content .img {
  z-index: 9999;
  width: 30px;
  height: 30px;
  background: #ff605f;
  margin: 0 auto;
  position: relative;
  border-radius: 50%;
  border: solid 2px #f6f6f6;
  box-shadow: 0 3px 8px -2px rgba(55, 60, 60, 0.7);
}

.form {
  background: #f6f6f6;
  text-align: center;
}

.form form {
  display: inline-block;
  width: 453px;
  padding: 20px;
  border-radius: 5px;
  margin-top: 10px;
}

.form form p {
  text-align: left;
}

.form form input[type=text], .form form [type=email] {
  width: 202px;
}

.form form input[type=text], .form form [type=email], .form form textarea {
  padding: 8px;
  border: solid 2px #dddddd;
  border-radius: 5px;
  margin-bottom: 10px;
}

.form form input[type=text]:focus, .form form [type=email]:focus, .form form textarea:focus {
  border: solid 2px #34cf75;
}

.form form textarea {
  height: 330px;
  width: 430px;
  min-width: 430px;
  min-height: 330px;
  max-width: 430px;
  max-height: 330px;
}

.form form input[type=submit], .form form [type=reset] {
  width: 80px;
  margin-left: 10px;
  padding: 2px;
  float: right;
  background: #fff;
  border: solid 2px #dddddd;
  border-radius: 5px;
}

.form form input[type=submit]:hover, .form form [type=reset]:hover {
  background: #34cf75;
  border: solid 2px #34cf75;
  color: #444444;
}

.social {
  float: right;
  margin-right: 40px;
}

.social a {
  float: left;
  display: inline-block;
  width: 50px;
  height: 50px;
  background: #f6f6f6;
  border-radius: 50%;
  text-align: center;
  line-height: 60px;
  color: #5ec2e7;
  margin-left: 16px;
  box-sizing: border-box;
}

.social a:hover {
  background: none;
  color: #f6f6f6;
  border: 2px solid #f6f6f6;
}
